	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: false,
			}
		},
		onLoad(){
			
		},
		methods: {
			getUserInfo(res) {
				if (!res.detail.iv) {
					uni.showToast({
						title: "您取消了授权,登录失败",
						icon: "none"
					});
					return false;
				}
			    let _this = this;
				
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						uni.getUserInfo({
							provider: 'weixin',  
							withCredentials:true,  
							success: function (infoRes) {  
								uni.request({
									url: _this.$Url + '/clmsj/login/index', // http://bull.hnw.net/index/bull/client_wash_info
									data: {
										encrypt_data:encodeURIComponent(infoRes.encryptedData),
										iv:encodeURIComponent(infoRes.iv),
										code:loginRes.code,
										type:2,  //type   1专业版  2零售版
									},
									method:'POST',
									success: (res) => {
										if (res.data.code == 0) {
											uni.setStorageSync("session_key",res.data.auth);
											uni.setStorageSync("userInfo",res.data.data.user);
											uni.navigateBack();  
										}else{
											uni.showToast({
												icon:'none',
												title:'请重新获取信息...'
											});
										}
									},
								})
							},  
							fail:function(infoRes){  
								console.log('infoRes='+JSON.stringify(infoRes))  
							}  
						});
					}
				});
				  
				
				
				
				
				
			    // App.getUserInfo(e, () => {
			    //   // 跳转回原页面
			    //   _this.onNavigateBack(1);
			    // });
			},
			onNavigateBack(delta) {
			    uni.navigateBack({
			      delta: Number(delta || 1)
			    });
			},
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack({
					delta: Number(2)
				});
			},
			toRegist(){
				this.$api.msg('去注册');
			},
			async toLogin(){
				this.logining = true;
				const {mobile, password} = this;
				/* 数据验证模块
				if(!this.$api.match({
					mobile,
					password
				})){
					this.logining = false;
					return;
				}
				*/
				const sendData = {
					mobile,
					password
				};
				const result = await this.$api.json('userInfo');
				if(result.status === 1){
					this.login(result.data);
                    uni.navigateBack();  
				}else{
					this.$api.msg(result.msg);
					this.logining = false;
				}
			}
		},

	}
